

.bg-custom-01 {
  --bs-bg-opacity: .14 !important;
}

.widget-actions {
  color: white !important;
  opacity: .88;
  &:hover {
    opacity: 1;
  }
}


// setup defaults
$primary: #1c4657;
$light: #e8edef;

// det card defaults
$card-border-width: 0;
$card-box-shadow: 0 0.125rem 0.25rem rgb(0 0 0 / 17%) !important;
$card-bg: #ffffff2b !important;
$card-color: #fff;
// $bg-gradient-card-top    : #507581;
// $bg-gradient-card-botton : #689c9f;

$modal-md: 640px;
$modal-lg: 640px;
$modal-xl: 640px;

